import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '@/views/index'
import Bazi from '@/views/bazi'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index
  },
  {
    path: '/bazi',
    name: 'bazi',
    component: Bazi
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
