<template>
    <div class="calendar-header clear">
        <div class="calendar-box">
            <span class="drawer" @click="handleMenu()">三</span>
            <div class="calendar-content" :style="{'text-align': 'combination'}">
                <span class="calendar-prev" @click="handlePrevMonth">&lt;</span>
                <span class="calendar-headDate" @click="handleDatePick">{{this.headTile}}</span>
                <span class="calendar-next" @click="handleNextMonth">></span>
            </div>
            <span class="calendar-today" @click="handleToday()"> 今天 </span>
        </div>
        <div v-if="visible">
            <vue-dselect v-model="visible" @on-ok="confirmHandle" :option="dpOptions"></vue-dselect>
        </div>
    </div>
</template>

<script>
    import VueDselect from "vue-component-dateselect";

    export default {
        props: {
            headTile: String
        },
        components: {
            VueDselect,
        },
        data() {
            let curDate = new Date();
            return {
                visible: false,
                showNum: 0,
                dpOptions: {
                    config: {
                        level: 3,
                        title: "请选择日期",
                        sureText: "确定",
                        closeText: "取消",
                        showAnimate: false,
                        maskCallback: () => this.visible = false,
                        fallback: () => this.visible = false,
                    },
                    data: {
                        currentDate: curDate.getFullYear() + '-' + curDate.getMonth() + "-" + curDate.getDate(),
                        beginDate: "1901-12-31",
                        endDate: "2099-12-31",
                    }
                },
            }
        },
        methods: {
            // 选择日期
            handleDatePick() {
                this.visible = true;
            },
            confirmHandle(y, m, d) {
                this.visible = false;
                this.$emit('confirmDatePick', y, m, d);
            },
            // 上一个月
            handlePrevMonth() {
                this.$emit('handlePrevMonth');
            },
            // 下一个月
            handleNextMonth() {
                this.$emit('handleNextMonth');
            },
            // 回到今天
            handleToday() {
                this.$emit('handleToday');
            },
            // 菜单
            handleMenu() {
                this.$emit('handleMenu');
            },

        }
    }
</script>

