                }

                .calendar-prev {
                    margin-right: 7px;
                }

                .calendar-next {
                    //background: url('../assets/img/right.png') no-repeat;
                    background-size: contain;
                }
            }

            .dispersion {
                width: initial;
                display: inline;
            }

            .calendar-headDate {
                vertical-align: middle;
                margin: 0 12px;
                font-size: 20px;
                color: #424953;
                -webkit-user-select: none;
                -webkit-touch-callout: none;
                -moz-user-select: none;
                -ms-user-select: none;
                user-select: none;
            }

            .calendar-today {
                position: absolute;
                top: 0;
                right: 10px;
                width: 46px;
                height: 26px;
                line-height: 26px;
                text-align: center;
                border: 1px solid #2061FF;
                border-radius: 4px;
                font-size: 14px;
