<template>
    <div class="bd">
        <div class="bazi-header clear">
            <div class="bazi-box">
                <span class="drawer" @click="handleMenu()">三</span>
                <div class="bazi-content" :style="{'text-align': 'combination'}">
                    生辰八字
                </div>
                <span class="bazi-right">  </span>
            </div>
        </div>
        <hr/>
        <div class="content">
            <div>
                <label>
                    <input type="radio" value="gli" v-model="rliSel" name="rli" @change="changeRli">
                    <span>公历</span>
                </label>
                <label :style="{marginLeft: '20px'}">
                    <input type="radio" value="nli" v-model="rliSel" name="rli" @change="changeRli">
                    <span>农历</span>
                </label>

                <label :style="{marginLeft: '30px'}">
                    <input type="radio" value="1" v-model="sexSel" name="sex" @change="handleLunisolar">
                    <span>男</span>
                </label>
                <label :style="{marginLeft: '20px'}">
                    <input type="radio" value="0" v-model="sexSel" name="sex" @change="handleLunisolar">
                    <span>女</span>
                </label>
            </div>
            <div :style="{marginTop: '10px'}">
                <div v-if="rliSel === 'gli'">
                    <label>
                        <select v-model="ySel" @change="handleLunisolar">
                            <option v-for="(item, index) in yearOpts" :value="item" :key="index">{{item}}</option>
                        </select>年
                    </label>
                    <label :style="{marginLeft: '5px'}">
                        <select v-model="mSel" @change="handleLunisolar">
                            <option v-for="(item, index) in monthOpts" :value="item.id" :key="index">{{item.name}}</option>
                        </select>月
                    </label>
                    <label :style="{marginLeft: '5px'}">
                        <select v-model="dSel" @change="handleLunisolar">
                            <option v-for="(item, index) in dayOpts" :value="item.id" :key="index">{{item.name}}</option>
                        </select>日
                    </label>
                    <label :style="{marginLeft: '5px'}" @change="handleLunisolar">
                        <select v-model="hSel">
                            <option v-for="(item, index) in hourOpts" :value="item.id" :key="index">{{item.name}}</option>
                        </select>时
                    </label>
                </div>
                <div v-if="rliSel === 'nli'">
                    <label>
                        <select v-model="ySel" @change="handleLunisolar">
                            <option v-for="(item, index) in yearOpts" :value="item" :key="index">{{item}}</option>
                        </select>年
                    </label>
                    <label :style="{marginLeft: '5px'}">
                        <input type="checkbox" v-model="leapM" @change="handleLunisolar"/>闰
                    </label>
                    <label>
                        <select v-model="mSel" @change="handleLunisolar">
                            <option v-for="(item, index) in month2Opts" :value="item.id" :key="index">{{item.name}}</option>
                        </select>月
                    </label>
                    <label :style="{marginLeft: '5px'}">
                        <select v-model="dSel" @change="handleLunisolar">
                            <option v-for="(item, index) in day2Opts" :value="item.id" :key="index">{{item.name}}</option>
                        </select>日
                    </label>
                    <label :style="{marginLeft: '5px'}">
                        <select v-model="hSel" @change="handleLunisolar">
                            <option v-for="(item, index) in hour2Opts" :value="item.id" :key="index">{{item.name}}</option>
                        </select>时
                    </label>
                </div>
            </div>
            <div :style="{marginTop: '10px'}">
                公历： {{ob?.format('YYYY年MM月DD日 HH时')}}
            </div>
            <div :style="{marginTop: '5px'}">
                农历： {{ob?.format('lYn年lMlD lH時')}}
            </div>
            <hr/>
            <div :style="{fontSize: '20px'}">
                <b>{{c8ex?.sex}}:&nbsp;&nbsp;</b><b>{{c8ex?.year}}</b>
                <b :style="{marginLeft: '10px'}">{{c8ex?.month}}</b>
                <b :style="{marginLeft: '10px'}">{{c8ex?.day}}</b>
                <b :style="{marginLeft: '10px'}">{{c8ex?.hour}}</b>
                <span v-if="c8ex?.missing" :style="{marginLeft: '10px'}">
                    (<b v-for="item in c8ex?.missing">{{item.name}}</b><b>空</b>)
                </span>
            </div>
            <div :style="{marginTop: '5px'}">
                <b>胎元:&nbsp;&nbsp;</b><span>{{c8ex?.embryo().name}} ({{c8ex?.embryo().takeSound}})</span>
            </div>
            <div>
                <b>命宫:&nbsp;&nbsp;</b><span>{{c8ex?.ownSign().name}} ({{c8ex?.ownSign().takeSound}})</span>
            </div>
            <div>
                <b>身宫:&nbsp;&nbsp;</b><span>{{c8ex?.bodySign().name}} ({{c8ex?.bodySign().takeSound}})</span>
            </div>
            <div :style="{marginTop: '10px'}">
                <table class="table">
                    <tr>
                        <th>五行:</th>
                        <td>{{c8ex?.year.stem.e5.name + c8ex?.year.branch.e5.name}}</td>
                        <td>{{c8ex?.month.stem.e5.name + c8ex?.month.branch.e5.name}}</td>
                        <td>{{c8ex?.day.stem.e5.name + c8ex?.day.branch.e5.name}}</td>
                        <td>{{c8ex?.hour.stem.e5.name + c8ex?.hour.branch.e5.name}}</td>
                    </tr>
                    <tr>
                        <th>纳音:</th>
                        <td>{{c8ex?.year.takeSound}}</td>
                        <td>{{c8ex?.month.takeSound}}</td>
                        <td>{{c8ex?.day.takeSound}}</td>
                        <td>{{c8ex?.hour.takeSound}}</td>
                    </tr>
                    <tr>
                        <th>十神:</th>
                        <td>{{c8ex?.year.stemTenGod.name}}</td>
                        <td>{{c8ex?.month.stemTenGod.name}}</td>
                        <td>{{c8ex?.day.stemTenGod.name}}</td>
                        <td>{{c8ex?.hour.stemTenGod.name}}</td>
                    </tr>
                    <tr>
                        <th rowspan ="2" >藏干:</th>
                        <td>
                            <span v-for="item in c8ex?.year.branch.hiddenStems">{{item.name}}</span>
                        </td>
                        <td>
                            <span v-for="item in c8ex?.month.branch.hiddenStems">{{item.name}}</span>
                        </td>
                        <td>
                            <span v-for="item in c8ex?.day.branch.hiddenStems">{{item.name}}</span>
                        </td>
                        <td>
                            <span v-for="item in c8ex?.hour.branch.hiddenStems">{{item.name}}</span>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <div v-for="item in c8ex?.year.branchTenGod">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.month.branchTenGod">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.day.branchTenGod">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.hour.branchTenGod">{{item.name}}</div>
                        </td>
                    </tr>
                    <tr>
                        <th>贵人:</th>
                        <td>
                            <div v-for="item in c8ex?.year.gods">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.month.gods">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.day.gods">{{item.name}}</div>
                        </td>
                        <td>
                            <div v-for="item in c8ex?.hour.gods">{{item.name}}</div>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
        <drawer :show="open"/>
    </div>
</template>

<script>
    import Drawer from '@/components/drawer'
    import lunisolar from 'lunisolar'
    import zhCn from 'lunisolar/locale/zh-cn'
    import {theGods} from '@lunisolar/plugin-thegods'
    import theGodsZhCn from '@lunisolar/plugin-thegods/locale/zh-cn'
    import { char8ex } from '@lunisolar/plugin-char8ex'
    import char8exZhCn from '@lunisolar/plugin-char8ex/locale/zh-cn'

    lunisolar.extend(theGods).extend(char8ex)
    lunisolar.locale(theGodsZhCn).locale(char8exZhCn).locale(zhCn)

    export default {
        name: "bazi",
        components: {
            Drawer
        },
        data() {
            let now = new Date();
            return {
                open: false,
                sexSel: '1',
                rliSel: 'gli',
                ySel: now.getFullYear(),
                mSel: now.getMonth() + 1,
                dSel: now.getDate(),
                hSel: now.getHours(),
                leapM: false,
                ob: null,
                c8ex: null,
            }
        },
        watch: {
        },
        computed: {
            yearOpts() {
                let items = [];
                for (let i = 1910; i <= 2099; i++) {
                    items.push(i)
                }
                return items;
            },
            monthOpts() {
                let items = [];
                for (let i = 1; i <= 12; i++) {
                    let item = {
                        id: i,
                        name: i,
                    }
                    items.push(item)
                }
                return items;
            },
            month2Opts() {
                let items = [];
                const tn = ["正", "二", "三", "四", "五", "六", "七", "八", "九", "十", "十一", "十二"];
                for (let i = 1; i <= 12; i++) {
                    let item = {
                        id: i,
                        name: tn[i - 1],
                    }
                    items.push(item)
                }
                return items;
            },
            dayOpts() {
                let items = [];
                for (let i = 1; i <= 31; i++) {
                    let item = {
                        id: i,
                        name: i,
                    }
                    items.push(item)
                }
                return items;
            },
            day2Opts() {
                let items = [];
                let tn = ["初一", "初二", "初三", "初四", "初五", "初六", "初七", "初八", "初九", "初十", "十一", "十二", "十三", "十四", "十五", "十六", "十七", "十八", "十九", "二十", "廿一", "廿二", "廿三", "廿四", "廿五", "廿六", "廿七", "廿八", "廿九", "三十"]
                for (let i = 1; i <= 30; i++) {
                    let item = {
                        id: i,
                        name: tn[i - 1],
                    }
                    items.push(item)
                }
                return items;
            },
            hourOpts() {
                let items = [];
                for (let i = 0; i <= 23; i++) {
                    let item = {
                        id: i,
                        name: i,
                    }
                    items.push(item)
                }
                return items;
            },
            hour2Opts() {
                return [
                    {id:0, name: '子(23~1)', label: '23 ~ 1'},
                    {id:2, name: '丑(1~3)', label: '1 ~ 3'},
                    {id:4, name: '寅(3~5)', label: '3 ~ 5'},
                    {id:6, name: '卯(5~7)', label: '5 ~ 7'},
                    {id:8, name: '辰(1~3)', label: '7 ~ 9'},
                    {id:10, name: '巳(7~9)', label: '9 ~ 11'},
                    {id:12, name: '午(11~13)', label: '11 ~ 13'},
                    {id:14, name: '未(13~15)', label: '13 ~ 15'},
                    {id:16, name: '申(15~17)', label: '15 ~ 17'},
                    {id:18, name: '酉(17~19)', label: '17 ~ 19'},
                    {id:20, name: '戌(19~21)', label: '19 ~ 21'},
                    {id:22, name: '亥(21~23)', label: '21 ~ 23'},
                ];
            },
        },
        mounted() {
            this.handleLunisolar();
        },
        methods: {
            handleMenu() {
                this.open = Math.random();
            },
            changeRli() {
                if (this.rliSel === 'nli') { // 农历
                    if (this.hSel % 2 !== 0) {
                        this.hSel += 1;
                        if (this.hSel === 24) {
                            this.hSel = 0;
                        }
                    }
                }
                this.handleLunisolar();
            },
            handleLunisolar() {
                if (this.rliSel === 'nli') {// 农历
                    let ymd = {
                        year: this.ySel,
                        month: this.mSel,
                        day: this.dSel,
                        hour: this.hSel / 2,
                        isLeapMonth: this.leapM,
                    }
                    try {
                        this.ob = lunisolar.fromLunar(ymd)
                    } catch (e) {
                        this.ob = null;
                        this.c8ex = null;
                    }
                } else {
                    let ymd = this.ySel + "-" + (this.mSel < 10 ? "0" + this.mSel : this.mSel) + "-" + this.dSel + " " + this.hSel + ":00";
                    this.ob = lunisolar(ymd)
                }
                if (this.ob != null) {
                    this.c8ex = this.ob.char8ex(this.sexSel);
                }

            }
        }
    }
</script>

